<template lang="pug">
    form.existed__group
        .existed__group-points
            .form__item
                label.label__name Тип группы:
                select(v-model="selectedType" :disabled='!editMode').existed__group-point
                    option(:value='group.type') {{group.type}}
                    option(value='Индивидуально' v-if="group.type!=='Индивидуально'") Индивидуально
                    option(value='Мини-группа' v-if="group.type!=='Минигруппа'") Мини-группа
            .form__item
                label.label__name Предмет:
                select(v-model="selectedSubject" :disabled='!editMode').existed__group-point
                    option(v-for='subject in departmentSubjects' :selected='subject.name!==group.subject.name') {{subject.name}}            
            .form__item
                label.label__name(for='class') Класс:
                select(v-model="selectedClass" :disabled='!editMode').existed__group-point
                    option(:value='item' v-for='item in 12' :selected='item!==group.groupClass') {{item}}
        .existed__group-points
            .form__item
                label.label__name Тип учебной программы:
                select(v-model="selectedProgrammeType" :disabled='!editMode').existed__group-point
                    option(:value='item' v-for='item in programmeTypes') {{item}}
            .form__item
                label.label__name Преподаватель:
                select(v-model="selectedTeacherId" :disabled='!editMode').existed__group-point
                    option(:value='teacher.id' v-for='teacher in departmentTeachers' :selected='teacher.id!==group.teacher.id') {{teacher.lastName}} {{teacher.firstName}}
            .form__item
                label.label__name Язык обучения:
                select(v-model="selectedLanguage" :disabled='!editMode').existed__group-point
                    option(:value='group.language') {{group.language}}
                    option(value='Казахский' v-if="'Казахский'!==group.language") Казахский
                    option(value='Русский' v-if="'Русский'!==group.language") Русский 
        .existed__group-points
            .existed__group-schedule
                .existed__group-schedule__title Расписание
                ul.existed__group-schedule__list
                    li(v-for='item in days').existed__group-schedule__item {{item}}
                        //existed-group-schedule(
                            :existenceOfDay='filteredDay(item)'
                            :group='group'
                            :day='item'
                            :editMode='editMode'
                        //)            
        .existed__group-buttons
            .existed__group-buttons-content
                button(@click.prevent='groupScholars()').existed__group-view
                    .view__scholars-button
                button(@click.prevent='groupAddScholar()').existed__group-add-scholar
                    .add__scholars-button
                        img(src='../../../assets/images/icons/add_scholar.svg')
                group-add-scholar(
                    v-if='groupAddScholarVisible'
                    v-on:saveScholar='saveScholar'
                    :groupId='group.id'
                    :scholars='scholars'
                    :groupClass='group.groupClass'
                )
            .buttons__existed-group
                button(@click.prevent='editMode=!editMode' v-if='!editMode').edit__existed-group-button Редактировать
                button(@click.prevent='deleteCurrentGroup' v-if='!editMode').remove__existed-group-button Удалить
                button(@click.prevent='editMode=!editMode' v-if='editMode').save__existed-group-button Сохранить
                button(@click.prevent='editMode=!editMode' v-if='editMode').reset__existed-group-button Отмена
        group-scholar-list(
            :groupId='group.id'
            v-if='groupScholarListVisible'
            v-on:removeItems='removeItems'
        )
</template> 
<script>
import { mapState, mapActions } from "vuex";
export default {  
    components: {
        existedGroupSchedule: () => import("../../admin/groups/existed-group-schedule"),
        groupScholarList: () => import("../../admin/groups/group-scholar-list"),
        groupAddScholar: () => import("../../admin/groups/group-add-scholar")
    },
    data() {
        return {
            editMode: false,
            groupScholarListVisible: false,
            groupAddScholarVisible: false,
            days: [
                'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'
            ],
            disabled: 0,
            selectedType: this.group.type,
            selectedSubject: this.group.subject.name,
            selectedClass: this.group.groupClass,
            selectedProgrammeType: this.group.eduProgrammeType,
            selectedTeacherId: this.group.teacher.id,
            selectedLanguage: this.group.language,
            subjects: [
                'Математика', 'Логика', 'География', 'Қазақ тілі'
            ],
            programmeTypes: [
                'основная',
                'ЕНТ',
                'подготовка в спец.школу',
                'подготовка к олимпиаде',
            ]
        }
    },
    created() {
        this.fetchScholars(),
        this.fetchDepartmentTeachers(this.department.id),
        this.fetchDepartmentSubjects(this.department.id)
    },
    computed: {
        ...mapState ("scholars", {
            scholars: state => state.scholars}),
        ...mapState ("groups", {
            groups: state => state.groups}),
        ...mapState ("departmentSubjects", {
            departmentSubjects: state => state.departmentSubjects}),
        ...mapState ("departmentTeachers", {
            departmentTeachers: state => state.departmentTeachers})
    },
    props: {
        group: Object,
        department: Object,
    },
    methods: {
        ...mapActions("departmentSubjects", ["fetchDepartmentSubjects"]),
        ...mapActions("departmentTeachers", ["fetchDepartmentTeachers"]),
        ...mapActions("scholars", ["fetchScholars"]),
        ...mapActions("groups", ["deleteGroup"]),
        groupScholars(){
            this.groupAddScholarVisible=false
            this.groupScholarListVisible=!this.groupScholarListVisible
        },
        groupAddScholar() {
            this.groupScholarListVisible=false
            this.groupAddScholarVisible=!this.groupAddScholarVisible
        },
        filteredDay(day) {
            if(this.group.groupSchedule.filter(ele=>{return ele.scheduleDay===day}).length) 
                return true
            else return false
        },
        saveScholar() {
            this.groupAddScholarVisible=!this.groupAddScholarVisible
        },
        removeItems() {
            this.groupScholarListVisible = false
        },
        deleteCurrentGroup() {
            this.deleteGroup(this.group.id)
        }
    }
}
</script>
